/*UI*/
.header {
  animation-duration: 3s;
  animation-name: nav;
  background-color: #FFFFFF;
  padding: 2rem 0 1rem 0;
}

@keyframes nav {
  from {
    transform: translate3d(0, -100%, 0);
    opacity: 0;
  }
  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}
