/*CAROUSEL*/
.banner img {
  width: 100%;
  background-position: center center;
  background-size: cover;
}

.banner-text {
  animation-duration: 4s;
  animation-name: texto;
}
.banner-text h5 {
  text-transform: uppercase;
}
.banner-text p {
  margin-bottom: 20rem;
}
@media (max-width: 1024px) {
  .banner-text p {
    font-size: 2.6rem;
    margin-bottom: 10rem;
  }
}
@media (max-width: 768px) {
  .banner-text p {
    font-size: 2.3rem;
    margin-bottom: 5rem;
  }
}
.banner-text h5, .banner-text p {
  font-family: "Noto Sans", sans-serif;
  font-size: 4rem;
  font-weight: bold;
  color: #ffffff;
  -webkit-text-stroke: 0.5px black;
}
@media (max-width: 768px) {
  .banner-text h5, .banner-text p {
    font-size: 2.3rem;
  }
}
@media (max-width: 1024px) {
  .banner-text h5, .banner-text p {
    font-size: 2.6rem;
  }
}

@keyframes texto {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}