.imagen-banner_nosotros{
    background-image: url(/build/img/banner-nosotros2.jpg);
    height:5rem;
    width: 100%;
    background-position: center center;
    background-size: cover;
    padding: 15rem 0;
    margin-bottom: 2rem; 
    
}

.nosotros{
    margin-bottom: 8rem;
}
.title{
    margin: 0;
}
.title-nos{
    font-weight: 600;
    color: $celesteOscuro;;
}

.titulo-nosotros{
    margin-bottom: 2rem;
    font-family: $primary_font;
    font-size: 5rem;
    font-weight: bold;
    color: #ffffff;
    -webkit-text-stroke: 0.5px black;
}

.texto-nosotros{
    line-height: 2.5rem;
    color: $celesteOscuro;
}
.img-nosotros{
    height: auto;
    width: 100%;
    border-radius: 5px;
    box-shadow: 10px 7px 33px 0px rgba(0,0,0,0.75);
-webkit-box-shadow: 10px 7px 33px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 10px 7px 33px 0px rgba(0,0,0,0.75);
}
.img-nosotros_mod{
    height: auto;
    width: 100%;
    border-radius: 5px;
    box-shadow: -10px 7px 33px 0px rgba(0,0,0,0.75);
-webkit-box-shadow: -10px 7px 33px 0px rgba(0,0,0,0.75);
-moz-box-shadow: -10px 7px 33px 0px rgba(0,0,0,0.75);
}


