
.content-footer{
    background-color:$negro;
    padding: 1.5rem ;
    
}

.nav-social__footer{
    display: flex;
    justify-content: space-around;
    background-color: $negro;
    
    @media(max-width: 320px){
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.nav__footer a{
    color: $blanco;
}


.media_social a img{
    @media(max-width: 768px){
        width: 25px;
        height: 25px;
    }
}

.info-footer{
    font-size: 1.5rem;
    text-align: center;
    color: $blanco;
}

.title-footer{
    font-size: 3rem;
    @media (max-width: 769px){
        font-size: 4rem;
    }
}

