.banner img{
    width:100%;
    //height: 100%;
    background-position: center center;
    background-size: cover;

}

.banner-text{
    animation-duration: 4s;
    animation-name: texto;
    h5{
        text-transform: uppercase;
    }

    p{
        margin-bottom: 20rem;
        @media (max-width: 1024px){
            font-size: 2.6rem;
            margin-bottom: 10rem;
        }
        @media (max-width: 768px){
            font-size: 2.3rem;
            margin-bottom: 5rem;
        } 
    }

    h5,p{
    font-family: $primary_font;
    font-size: 4rem;
    font-weight: bold;
    color: #ffffff;
    -webkit-text-stroke: 0.5px black;
        @media (max-width: 768px){
            font-size: 2.3rem;
            
        } 
        @media (max-width: 1024px){
            font-size: 2.6rem;
        }
    }
} 
@keyframes texto {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}