.imagen-banner_disney{
    background-image: url(/build/img/banner-disney.webp);
    height:5rem;
    width: 100%;
    background-position: center center;
    background-size: cover;
    padding: 15rem 0;
    margin-bottom: 2rem; 
    
}
.parks-info{
    border: 1px solid $gris;
    border-radius: 30px;
    padding: 3rem;
    line-height: 2.8rem;
    font-family: $primary_font;
    font-weight: 400;
    margin: 2rem;

    h1{
        color: #655f5f;
        font-size: 4rem;
        text-transform: uppercase;
    }
    p{
        color: $celesteOscuro;
    }
    h3{
        color: #655f5f;
        font-size: 2.5rem;
        text-transform: uppercase;
    }
    h4{
        text-transform: uppercase;
        font-family:'Noto Sans', sans-serif;
        font-weight: 300;
        font-size: 1.8rem;
        text-align: center;
        color:#655f5f;
    }
}

.themes-parks{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.park {
    img{
    width: 200px;
    height: 150px;
    }
}

